.email-container {
    display: flex;
    flex-direction:column;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 16px;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
}

.from {
    pointer-events: none;
}

.subject {
    pointer-events: none;
}

.address {
    pointer-events: none;
}

.time {
    pointer-events: none;
}

.bg-color-read {
    background-color: grey;
}